import { useGlobalToast } from '@cetus/design'
import useExplorer from '@cetus/hooks/src/useExplorer'
import useFastModeTransaction from '@cetus/hooks/src/useFastModeTransaction'
import useMsafeTransaction from '@cetus/hooks/src/useMsafeTransaction'
import { useSigner } from '@cetus/hooks/src/useSigner'
import useTrack from '@cetus/hooks/src/useTrack'
import useTransactionModal from '@cetus/hooks/src/useTransactionModal'
import { useSdk } from '@cetus/sdk-factory'
import { useAccountStore } from '@cetus/stores'
import { CommonTypeInfo, ToastType, TransactionOption } from '@cetus/types/src/common-types'
import { trackShioEvent } from '@cetus/utils'
import { base64ToUint8Array, parseOwnerBalanceChanges, sleepTime } from '@cetus/utils/src/common'
import { handleErrorMessages } from '@cetus/utils/src/error'
import { MSafeWallet } from '@msafe/sui-wallet'
import { Transaction, TransactionDataBuilder } from '@mysten/sui/transactions'
import { executeAuction } from 'shio-sdk'

export default function useTransaction() {
  const { signTransactionBlock, executeTransactionBlock, signAndExecuteTransactionBlock } = useSigner()
  const { currentAccount } = useAccountStore()
  const clmmSdk = useSdk('clmm')
  const { transactionConfirmation, transactionSubmitted, transactionSuccess, transactionRejected, closeTransactionModal } = useTransactionModal()
  const { submittedTsToast, failedTsToast, successTsToast, closeToast } = useGlobalToast()
  const { getExplorerUrl } = useExplorer()
  const { getFastModeTx } = useFastModeTransaction()
  const { getMsafeRes } = useMsafeTransaction()
  const { trackShio, trackSwap } = useTrack()

  const MAX_RETRIES = 5
  const RETRY_DELAY = 2000

  async function getTransactionStatus(digest: string, attempt = 1): Promise<any> {
    console.log('ðŸš€ ~ file: useTransaction.tsx:24 ~ getTransactionStatus ~ Checking transaction status, attempt:', attempt)

    try {
      let response
      try {
        response = await clmmSdk!.FullClient.getTransactionBlock({
          digest,
          options: {
            showEvents: true,
            showEffects: true,
            showBalanceChanges: true,
            showInput: true,
            showRawInput: true,
            showObjectChanges: true
          }
        })
      } catch (error) {
        response = await clmmSdk!.FullClient.getTransactionBlock({
          digest,
          options: {
            showEvents: true,
            showEffects: true
          }
        })
      }

      console.log('ðŸš€ ~ file: useTransaction.tsx:28 ~ getTransactionStatus ~ response:', response)

      if (response) {
        return response
      }
      throw new Error('Empty response')
    } catch (error) {
      console.error(`Attempt ${attempt} failed:`, error)

      if (attempt >= MAX_RETRIES) {
        throw new Error('Failed to fetch transaction status after maximum retries.')
      }

      // å»¶è¿ŸåŽç»§ç»­ä¸‹ä¸€æ¬¡è½®è¯¢
      await sleepTime(RETRY_DELAY)
      return getTransactionStatus(digest, attempt + 1)
    }
  }

  type BuildTransactionCallback = () => Promise<Transaction>

  const signAndExecuteTransaction = async (
    tx: Transaction | BuildTransactionCallback,
    toastType: ToastType,
    {
      useDevInspect = false,
      useMev = false,
      txAction = 'signTransactionBlock',
      showSuccessModal = true,
      useFastMode = false,
      maxCapForGas = '',
      customGasPrice = '',
      msafeParams,
      otherParams
    }: TransactionOption = {}
  ) => {
    console.log('ðŸš€ ~ useTransaction ~ msafeParams:', msafeParams)
    console.log('ðŸš€ ~ useTransaction ~ otherParams:', otherParams)
    console.log('ðŸš€ ~ file: useTransaction.tsx:76 ~ useTransaction ~ option:', {
      useDevInspect,
      useMev,
      txAction,
      tx,
      toastType,
      useFastMode,
      maxCapForGas,
      customGasPrice,
      msafeParams
    })

    // if (useFastMode && maxCapForGas) {
    //   await getFastModeTx(tx, maxCapForGas)
    // }

    const txConfirmToastId = toastType.transactionId ? toastType.transactionId : new Date().getTime().toString()
    toastType.transactionId = txConfirmToastId
    // ç¡®è®¤ä¸­
    transactionConfirmation(toastType)

    const isSwap = msafeParams?.action === 'AggregatorSwap'
    let digest = ''

    try {
      let response
      let transactionBlockBytes

      let transaction: Transaction
      if (typeof tx === 'function') {
        transaction = await tx()
      } else {
        transaction = tx
      }
      transaction.setSender(currentAccount?.address)
      let startExecuteTime
      if (useFastMode && maxCapForGas && customGasPrice) {
        // Fast modeæ¨¡å¼
        console.log('ðŸš€ðŸš€ðŸš€ ~ useTransaction.tsx:135 ~ useTransaction ~ res:')
        const res = await getFastModeTx(transaction, maxCapForGas, customGasPrice)
        response = res.result
        startExecuteTime = res.startExecuteTime
        transactionBlockBytes = res.transactionBlockBytes
      } else if (MSafeWallet?.inMSafeWallet()) {
        if (msafeParams) {
          response = await getMsafeRes(transaction, msafeParams)
        } else {
          throw Error('The current operation does not support msafe wallet')
        }
      } else {
        // Defaultæ¨¡å¼
        if (txAction === 'signTransactionBlock') {
          const signTx = await signTransactionBlock(transaction)
          startExecuteTime = new Date().getTime()
          console.log('ðŸš€ ~ file: useTransaction.tsx:94 ~ useTransaction ~ signTx:', signTx)

          const { bytes, signature } = signTx
          transactionBlockBytes = signTx.bytes
          if (useMev) {
            try {
              const shioRes = await executeAuction(bytes, signature, 500)
              console.log('ðŸš€ ~ file: useTransaction.tsx:92 ~ useTransaction ~ shioRes: success', shioRes)
            } catch (error) {
              console.log('ðŸš€ ~ file: useTransaction.tsx:95 ~ useTransaction ~ shioRes : error:', error)
            }
          }
          console.log('ðŸš€ ~ useTransaction ~ useMev:', useMev)
          response = await executeTransactionBlock(bytes, signature, useDevInspect)
          console.log('ðŸš€ ~ useTransaction ~ response:', response)
        } else {
          response = await signAndExecuteTransactionBlock(transaction, useDevInspect)
        }
      }

      digest = response?.digest
      console.log('ðŸš€ ~ file: useTransaction.tsx:24 ~ signAndExecuteTransaction ~ response:', response)

      if (!digest && transactionBlockBytes) {
        const uint8ArrayData = base64ToUint8Array(transactionBlockBytes)
        digest = TransactionDataBuilder.getDigestFromBytes(uint8ArrayData)
      }

      toastType.tx = digest
      transactionSubmitted(toastType)
      if (!toastType.isSwapWidget) {
        submittedTsToast(toastType)
      }
      // å…³é—­è¿›åº¦å¼¹çª—
      closeToast(txConfirmToastId)

      let res
      if (response?.effects?.status.status === 'success') {
        res = response
      } else {
        console.log('ðŸš€ðŸš€ðŸš€ ~ useTransaction.tsx:191 ~ useTransaction ~ digest:', digest)
        res = await getTransactionStatus(digest, 0)
      }

      if (!res || res.effects?.status.status === 'failure') {
        const errorString = handleError(toastType, res.effects?.status.error)

        // è°·æ­Œæ‰“ç‚¹
        if (isSwap) {
          trackSwap(
            {
              ...otherParams,
              useMev,
              useFastMode
            },
            errorString,
            digest
          )
        }
      } else {
        // æˆåŠŸ
        if (startExecuteTime) {
          const endExecuteTime = new Date().getTime()
          const timestampMs: number = res?.timestampMs || 0
          const endTime = timestampMs && Number(timestampMs) > startExecuteTime ? Math.min(endExecuteTime, Number(timestampMs)) : endExecuteTime
          const executeTime = endTime - startExecuteTime
          console.log('ðŸš€ ~ useTransaction ~ executeTime:', executeTime)

          if (executeTime < 1000) {
            toastType.executeTime = executeTime
          }
        }

        const balanceChanges = parseOwnerBalanceChanges(res, currentAccount?.address, false)
        console.log('ðŸš€ðŸš€ðŸš€ ~ useTransaction.tsx:165 ~ useTransaction ~ res:', res)

        console.log('ðŸš€ ~ file: useTransaction.tsx:48 ~ signAndExecuteTransaction ~ balanceChanges:', balanceChanges)
        if (toastType.getShowInfo) {
          const info = toastType.getShowInfo('success', balanceChanges, res)
          toastType.getShowInfo = (_: any) => {
            return info
          }
        }
        if (showSuccessModal) {
          transactionSuccess(toastType)
        } else {
          closeTransactionModal()
        }
        if (!toastType.isSwapWidget) {
          toastType.transactionId = undefined
          successTsToast(toastType)
        }
        if (res && !res.digest && digest) {
          res.digest = digest
        }

        // é…åˆshioæ‰“ç‚¹
        if (msafeParams) {
          try {
            trackShioEvent(msafeParams.action, useFastMode || useMev, digest)
          } catch (e) {
            console.log('ðŸš€ ~ useTransaction ~ trackShioEvent error:', e)
          }
        }

        // è°·æ­Œæ‰“ç‚¹
        if (isSwap) {
          trackSwap({ ...otherParams, useMev, useFastMode }, '', digest)
        }
        return res
      }
    } catch (error) {
      console.log('ðŸš€ðŸš€ðŸš€ ~ useTransaction.tsx:253 ~ useTransaction ~ error:', error)
      // ToDo: åº”è¯¥æ‰€æœ‰æ“ä½œå½“rpcæœ‰é—®é¢˜æ—¶éƒ½éœ€æŠ¥é”™ï¼ŒåŽé¢éœ€ç»Ÿä¸€å¤„ç†
      if (txConfirmToastId) {
        closeToast(txConfirmToastId)
      }

      const errorString = handleError(toastType, error)

      // è°·æ­Œæ‰“ç‚¹
      if (isSwap) {
        trackSwap({ ...otherParams, useMev, useFastMode }, errorString, digest)
      }
    }

    return undefined
  }

  const handleError = (toastType: ToastType, error: any) => {
    const errorString = handleErrorMessages(error)

    console.error('ðŸš€ ~ file: useTransaction.tsx:270 ~ handleError ~ error:', { error, errorString })

    if (toastType.getShowInfo) {
      const info = toastType.getShowInfo('rejected')

      toastType.getShowInfo = (_: any) => {
        info.toastDescriptionContent = errorString
        info.modalDescriptionText = errorString
        return info
      }
      transactionRejected(toastType)
      if (!toastType.isSwapWidget) {
        failedTsToast(toastType)
      }
    } else {
      toastType.getShowInfo = (_: any) => {
        const info: CommonTypeInfo = {}
        info.toastDescriptionContent = errorString
        info.modalDescriptionText = errorString
        return info
      }
      transactionRejected(toastType)
      if (!toastType.isSwapWidget) {
        failedTsToast(toastType)
      }
    }

    return errorString
  }

  return {
    signAndExecuteTransaction,
    transactionSuccess,
    getTransactionStatus,
    transactionConfirmation,
    transactionRejected,
    handleError
  }
}
