import { useTransactionStore } from '@cetus/stores/src'
import { ToastType } from '@cetus/types/src/common-types'
import { useEffect, useRef } from 'react'

export default function useTransactionModal() {
  const { setTransactionData, setTransactionModalVisible, manualCloseIdList } = useTransactionStore()

  const manualCloseIdListRef = useRef(manualCloseIdList)

  useEffect(() => {
    manualCloseIdListRef.current = manualCloseIdList
  }, [manualCloseIdList])

  const isDisableModal = (id?: string) => {
    if (id) {
      const isDisable = manualCloseIdListRef.current.includes(id)
      if (isDisable) {
        setTransactionModalVisible(false)
      }
      return isDisable
    }

    return false
  }

  const transactionConfirmation = (props: ToastType) => {
    if (isDisableModal(props.transactionId)) {
      return
    }
    setTransactionData({
      ...props,
      status: 'confirmation'
    })
    setTransactionModalVisible(true)
  }

  const transactionSubmitted = (props: ToastType) => {
    if (isDisableModal(props.transactionId)) {
      return
    }
    setTransactionModalVisible(false)
    setTransactionData({
      ...props,
      status: 'submitted'
    })
    setTransactionModalVisible(true)
  }

  const transactionSuccess = (props: ToastType) => {
    if (isDisableModal(props.transactionId)) {
      return
    }
    setTransactionModalVisible(false)
    setTransactionData({
      ...props,
      status: 'success'
    })
    setTransactionModalVisible(true)
  }

  const transactionRejected = (props: ToastType, list = manualCloseIdList) => {
    console.log('ðŸš€ ~ file: useTransactionModal.ts:10 ~ useTransactionModal ~ id 55:', manualCloseIdList)
    if (isDisableModal(props.transactionId)) {
      return
    }
    setTransactionModalVisible(false)
    setTransactionData({
      ...props,
      status: 'rejected'
    })
    setTransactionModalVisible(true)
  }

  const closeTransactionModal = () => {
    setTransactionModalVisible(false)
  }

  return { transactionConfirmation, transactionSubmitted, transactionSuccess, transactionRejected, closeTransactionModal }
}
