export function isUserRejectedError(error: any) {
  try {
    return error && error.includes('user') && error.includes('reject')
  } catch (error1) {
    return false
  }
}

/**
 * åˆ›å»ºæ± å­å­˜åœ¨é”™è¯¯
 * @param error
 * @returns
 */
export function isCreatePoolExistError(error: any) {
  const errorString = String(error)
  if (errorString.indexOf('Some("create_pool_internal")') > -1 && errorString.indexOf(` }, 1`) > -1) {
    return true
  }
  return false
}

export function isPriceSlippageError(error: any) {
  const errorString = String(error)
  if (
    errorString.indexOf('withdraw_with_proof') > -1 ||
    errorString.indexOf('check_coin_threshold') > -1 ||
    errorString.indexOf('Identifier("balance")') > -1
  ) {
    return true
  }

  if (
    errorString.indexOf('remove_liquidity') > -1 &&
    (errorString.indexOf('in command 1') > -1 ||
      errorString.indexOf('in command 2') > -1 ||
      errorString.indexOf('in command 4') > -1 ||
      errorString.indexOf('in command 5') > -1)
  ) {
    return true
  }

  if (
    errorString.indexOf('repay_add_liquidity') > -1 &&
    (errorString.indexOf('in command 1') > -1 ||
      errorString.indexOf('in command 2') > -1 ||
      errorString.indexOf('in command 3') > -1 ||
      errorString.indexOf('in command 4') > -1 ||
      errorString.indexOf('in command 5') > -1)
  ) {
    return true
  }
  return false
}

export function isNotFindTxHashError(error: any) {
  const errorString = String(error)
  if (errorString.includes('Could not find the referenced transaction')) {
    return true
  }
  return false
}

export function isTRPCClientError(error: any) {
  const errorString = String(error)
  if (errorString.includes('TRPCClientError:')) {
    return true
  }
  return false
}

export function isSuiHTTPStatusError(error: any) {
  const status = error.status
  const errorString = String(error)

  if ([429, 503, 502, 400].includes(status) || errorString.includes('Could not find the referenced object')) {
    return true
  }
  return false
}

export function isMaxCapError(error: any) {
  const errorString = String(error)
  if (errorString.includes('Max cap reached.')) {
    return true
  }
  return false
}

export function isFastModeServiceError(error: any) {
  const errorString = String(error)
  if (errorString.includes('Fast Mode service error')) {
    return true
  }
  return false
}

export function isNotSupportMsafe(error: any) {
  const errorString = String(error)
  if (errorString.includes('The current operation does not support msafe wallet')) {
    return true
  }
  return false
}

export function isVaultHardCapError(error: any) {
  const errorString = String(error)
  if (errorString.indexOf('hard_cap_reached') > -1) {
    return true
  }
  return false
}

type ErrorMatcher = (error: any) => boolean
type ErrorMessage = string | ((error: any) => string)

const errorMap: { matcher: ErrorMatcher; message: ErrorMessage }[] = [
  { matcher: isUserRejectedError, message: 'User rejected the request' },
  {
    matcher: isNotFindTxHashError,
    message: 'Oops. Failed to sync data from your txid. Please check your txn status from explorer or from your wallet.'
  },
  { matcher: isTRPCClientError, message: (error: any) => String(error).split(':')[1] },
  {
    matcher: isSuiHTTPStatusError,
    message: 'RPC loading failed. Try resubmission or check your network. You can also switch an RPC in settings to try again.'
  },
  {
    matcher: isMaxCapError,
    message: 'Max cap reached. This is to prevent your txn from overpaying. You can choose to edit your gas max cap in settings if needed.'
  },
  { matcher: isFastModeServiceError, message: 'Fast Mode service error, please switch to the Default Transaction Mode or try again later.' },
  { matcher: isNotSupportMsafe, message: 'The current operation does not support msafe wallet.' },
  {
    matcher: (error: any) => String(error).includes('Transaction validator signing failed'),
    message: 'Oops. Failed to pull your txid. Please check your txn status from explorer or from your wallet.'
  },
  { matcher: isPriceSlippageError, message: 'Exceeded price slippage' },
  { matcher: isCreatePoolExistError, message: 'Pool already exists' },
  { matcher: isVaultHardCapError, message: 'Deposit Exceeds Cap' }
]

export function handleErrorMessages(error: any): string {
  for (const { matcher, message } of errorMap) {
    if (matcher(error)) {
      return typeof message === 'function' ? message(error) : message
    }
  }

  return 'Transaction failed'
}
